/* Banner */
#head {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100vh;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.6) url("./images/banner-1.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;

  /* background: #f4a046; */
  /* background: linear-gradient(to bottom, #f8a44a, #d66e06); */
  /* margin-top: 100px; */
}

.banner-content {
  align-items: center;
  justify-content: center;
  margin: 10vw;
}

.banner-content h1 {
  color: #f1f1f1;
  color: linear-gradient(to bottom, #f8a44a, #d66e06);
  ;
  font-size: 84px;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.4);
  font-weight: 900;
  text-align: left;
  font-family: "Open sans", Helvetica, Arial;
}
.banner-content p {
  margin-top: 20px;
  color: #f1f1f1;
  font-size: 26px;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.4);
  font-weight: 700;
  text-align: left;
  font-family: "Open sans", Helvetica, Arial;
}



.banner-content button {
  padding: 10px;
  border: 2px solid white;
  background: transparent;
  color: white;
  border-radius: 5px;
  transition: all 0.5s;
  min-width: 180px;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content button:hover {
  padding: 10px;
  border: 2px solid rgb(223, 117, 25);
  background:  rgb(223, 117, 25);
  color: white;
  border-radius: 5px;

}

.banner-content button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  font-family: "Open sans", Helvetica, Arial;
}



.banner-content button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content button:hover span {
  padding-right: 25px;
}

.banner-content button:hover span:after {
  opacity: 1;
  right: 0;
}





